import { PerspectiveCamera, RenderTexture, Text, MeshDistortMaterial } from '@react-three/drei';

import React, { useRef } from 'react'

function Mongo() {

  const textRef = useRef();



  return (<mesh>
    <cylinderGeometry />
    <MeshDistortMaterial
      color={"white"}
      attach={"material"}
      distort={"0.1"}
      speed={"3"} >
      <RenderTexture attach={"map"}>
        <PerspectiveCamera
          makeDefault
          position={[0, 0, 10]}
        />
        <color attach={"background"} args={["#001e2b"]} siz/>
        <Text fontSize={1} color={"#00ed64"} ref={textRef}>
          MongoDB
        </Text>
      </RenderTexture>
    </MeshDistortMaterial>
  </mesh>)
}

export default Mongo