import { PerspectiveCamera, RenderTexture, Text, MeshDistortMaterial } from '@react-three/drei'
import { useFrame } from '@react-three/fiber'
import React, { useRef } from 'react'

function React3D() {

  const textRef = useRef();
  useFrame(state => (textRef.current.position.x = Math.sin(state.clock.elapsedTime) * 2))


  return (<mesh>
    <boxGeometry />
    <MeshDistortMaterial
      color={"white"}
      attach={"material"}
      distort={"0.5"}
      speed={"3"} >
      <RenderTexture attach={"map"}>
        <PerspectiveCamera
          makeDefault
          position={[0.5, -0.7, 7]}
        />
        <color attach={"background"} args={["#61DBFB"]} />
        <Text fontSize={2} color={"#2d415c"} ref={textRef}>
          REACT
        </Text>
      </RenderTexture>
    </MeshDistortMaterial>
  </mesh>)
}

export default React3D